<template>
	<div>
		<ContentHeader title="Farmasi" url="/farmasi" subTitle="Konfirmasi Obat" />
		<div class="content-header">
			<div class="container-fluid">
				<div class="row justify-content-center">
		            <div class="col">
		            	<div class="card">
		            		<div class="card-header row">
		            			<h3 class="card-title">Konfirmasi Obat Pasien</h3>
		            		</div>
		            		<div class="card-body">
		            			<form @submit.prevent="prosesKonfirmasi">
					                <div class="px-lg-2 ">
					                  <div class="form-group">
					                    <label for="nomor_rekam_medis">No Rekam Medis</label>
					                    <input type="text" class="form-control" id="nomor_rekam_medis" name="nomor_rekam_medis" disabled="" v-model="rekam_medis.nomor_rekam_medis">
					                  </div>
					                  <div class="form-group">
					                    <label for="diagnosa">Diagnosa </label>
					                    <div class="row">
					                    	<div class="col">
					                    		<table class="table table-bordered table-hover">
												  <thead>
												    <tr>
												      <th scope="col" class="text-sm">NO</th>
												      <th scope="col" class="text-sm">KODE</th>
												      <th scope="col" class="text-sm">NAMA DIAGNOSA</th>
												    </tr>
												  </thead>
												  <tbody>
												    <tr v-for="(row, index) in rekam_medis_result" :key="row.id">
												    	<td>{{ index + 1 }}</td>
												    	<td>{{ row.kode_icdx }}</td>
												    	<td >{{ row.nama_icdx }}</td>
												    </tr>
												  </tbody>
												</table>
					                    	</div>
					                    	<div class="col"></div>
					                    </div>
					                  </div>
					                  <div class="form-group">
					                  	<div class="row">
					                  		<div class="col-12 col-md-4">
					                  			<label for="resep_obat">Tambah Resep Obat</label>
							                  	<select class="custom-select form-control" id="resep_obat" name="resep_obat" v-model="resepObatModel" @change="tambahResep(resepObatModel)">
			      									<option v-for="(row, index) in resepData" :key="row.id" v-bind:value="index">{{ row.kode }} - {{ row.nama }}</option>
			    								</select>
					                  		</div>
					                  		<div class="col col-md-1"></div>
					                  		<div class="col-12 col-md-7">
					                  			<label class="mt-1">  </label>
					                  			<table class="table table-bordered table-hover">
												  <thead>
												    <tr>
												      <th scope="col" class="text-sm">NO</th>
												      <th scope="col" class="text-sm">KODE</th>
												      <th scope="col" class="text-sm">NAMA RESEP</th>
												      <th scope="col" class="text-sm">STOK</th>
												      <th scope="col" class="text-sm">ATURAN PAKAI</th>
												      <th scope="col" class="text-sm">ACTION</th>
												    </tr>
												  </thead>
												  <tbody>
												    <tr v-for="(row, index) in resep" :key="row.id">
												    	<td>{{ index + 1 }}</td>
												    	<td>{{ row.kode }}</td>
												    	<td>{{ row.nama }}</td>
												    	<td>{{ row.stok }}</td>
												    	<td>{{ row.signa }}</td>
												    	<td><a class="btn btn-block btn-outline-danger btn-xs" @click="hapusDataResep(row.id)" v-if="row.hapus">Hapus</a></td>
												    </tr>
												  </tbody>
												</table>
					                  		</div>
					                  	</div>
					                  </div>
					                  <button type="submit" class="btn btn-primary d-flex align-items-center">
										  <div>
										  	Submit
										  </div>	
										  <div>
						                    <svg v-if="loading" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style=" background: none; display: block; shape-rendering: auto;" width="200px" height="200px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
						                      <g>
						                        <path d="M50 15A35 35 0 1 0 74.74873734152916 25.251262658470843" fill="none" stroke="#ffffff" stroke-width="12"></path>
						                        <path d="M49 3L49 27L61 15L49 3" fill="#ffffff"></path>
						                        <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1s" values="0 50 50;360 50 50" keyTimes="0;1"></animateTransform>
						                      </g>
						                    </svg>
						                  </div>
										</button>
					                </div>
					                <!-- /.card-body -->					               
					              </form>
		            		</div>
		            	</div>
		            </div>
		        </div>
			</div>
		</div>
	</div>
</template>

<script>
	import ContentHeader from '@/components/ContentHeader2'

	import { useRoute } from 'vue-router'
	import { ref, onMounted, computed } from 'vue'
	import axios from 'axios'
	import Swal from 'sweetalert2'
	import router from '@/router'
	import store from '@/store'

	export default{
		components: {
			ContentHeader: ContentHeader
		},
		setup(){
			const user = computed(() => store.getters['auth/user'])
			const loading = ref(false)

			const route = useRoute()
			const kunjungan_id = route.params.id
			const rekam_medis = ref([])
			const rekam_medis_update = ref({
				'kunjungan_id': kunjungan_id,
				'resep_id': '',
				'nama_resep': ''
			})

			const rekam_medis_result = ref([])
			const resep_id = ref([])
			const nama_resep = ref([])
			const resepData = ref([])

			const getRekamMedis = async () => {
				let response = await axios.get(`api/rekam_medis/getWhereId/${kunjungan_id}`)
				rekam_medis.value = response.data
				for (var i = 0; i < response.data.kode_icdx.length; i++) {
					rekam_medis_result.value.push({
						'kode_icdx': response.data.kode_icdx[i],
						'nama_icdx': response.data.nama_icdx[i]
					})

					if (response.data.resep_id[i] != null) {
						resep_id.value.push(response.data.resep_id[i])
						nama_resep.value.push(response.data.nama_resep[i])
					}

				}
			}

			const getResepData = async () => {
				let response = await axios.get(`api/resep_obat/get/${user.value.cabang_id}`)
				resepData.value = response.data
			}


			// ---------Get data tindakan & resep obat---------
			const resep = ref([])
			const resepObatModel = ref()
			const getResep = async () => {
				let response = await axios.get(`api/rekam_medis/getWhereId/${kunjungan_id}`)

				console.log(response.data)
				
				for (var i = 0; i < resep_id.value.length; i++) {
					let response_resep = await axios.get(`api/resep_obat/getWhereId/${resep_id.value[i]}`)
					
					resep.value.push({
						'id': i,
						'resep_id': response_resep.data.id,
						'kode': response_resep.data.kode,
						'nama': response_resep.data.nama,
						'stok': response_resep.data.stok,
						'signa': response_resep.data.signa,
						'hapus' : true
					})
				}
			}

	    	// ---------End Get data tindakan & resep obat---------

	    	const tambahResep = (id) => {
				let data = resepData.value[id]

				resep.value.push({
					'id': id,
					'resep_id': data.id,
					'kode': data.kode,
					'nama': data.nama,
					'stok': data.stok,
					'signa': data.signa,
					'hapus' : true
				})

				resep_id.value.push(data.id)
				nama_resep.value.push(data.nama)
			}

			const hapusDataResep = (index) => {
				for(let j = 0; j < resep.value.length; j++) {
					if (resep.value.[j].id == index) {
						resep.value.splice(j, 1)

						resep_id.value.splice(j, 1)
						nama_resep.value.splice(j, 1)
					}
				}

				if (resep.value.length == 0) {
					resepObatModel.value = ''
				}
			}


			const prosesKonfirmasi = () => {
				loading.value = true

				rekam_medis_update.kunjungan_id = kunjungan_id
				rekam_medis_update.resep_id = resep_id.value
				rekam_medis_update.nama_resep = nama_resep.value

				
				axios.put(`api/rekam_medis/konfirmasi/${rekam_medis.value.id}`, rekam_medis_update)
				.then((response) => {
					Swal.fire({
					  title: 'Berhasil!',
					  text: 'Obat Berhasil Dikonfirmasi',
					  icon: 'success',
					  confirmButtonText: 'Ok'
					})	
					loading.value = false
		    		console.log(response.data)
		    		stop()
		    		router.push('/farmasi')
		    	})
		    	.catch((error) => {
		    		Swal.fire({
					  title: 'Gagal!',
					  text: 'Gagal mengonfirmasi obat',
					  icon: 'error',
					  confirmButtonText: 'Ok'
					})	
					loading.value = false
		    		console.log(error)
		    	})	
				
					
			}

			onMounted(() => {
				getRekamMedis()
				getResep()
				getResepData()
			})

			return { rekam_medis, getRekamMedis, prosesKonfirmasi, getResep, resep, rekam_medis_result, getResepData, resepData, tambahResep, hapusDataResep, resepObatModel, loading }
		}	
	}
</script>